import React from 'react';

import AOS from 'aos';
import 'aos/dist/aos.css'; 
AOS.init();

const Service = ({services}, ) => {
  
  return (
    <div className='service-section' name="Service">
        
        <div className='service-header'>
            <h2> </h2>
        </div>
        
        <div className='service-cards-section'>
          
          { services.map((div) =>  
          <div key={div.id} className='card-service-button' data-aos="flip-right"
            
           data-aos-offset="200"
           data-aos-delay="50"
           data-aos-duration="1000"
           data-aos-easing="ease-in-out"
           data-aos-mirror="true"
           data-aos-once="false"
           data-aos-anchor-placement="top-center" 
          >      
            <div className='card-service'>
              <div className='card-header'>
                <span>{div.header01} <br/> {div.header02}</span>
              </div>
              
              <div className='card-list'>
                <ul>
                    <li> <img src={div.image1}/> {div.title1} </li>
                    <li> <img src={div.image2}/> {div.title2} </li>
                    <li> <img src={div.image3}/> {div.title3} </li>
                    <li> <img src={div.image4}/> {div.title4} </li>
                    <li> <img src={div.image5}/> {div.title5} </li> 
                </ul>
              </div>
              
              <div className='card-price'>
               <span className='span-top'></span> 
               <span className='span-bottom'> {div.price}</span>  
              </div>  
            </div>
            
            <div className='service-button'>
              <button></button>
            </div>
          
          </div>
          
          )}
          
        
        </div>
        
    </div>
  )
}

export default Service