import React from 'react';

import { Link, Button, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import LogoPic from '../assets/images/logo-pic.png'

const Footer = () => {
  return (
    <>
    <div className='footer'>
      <div className='footer-top'>
        <div className='footer-logo-section'>
          <div className='footer-logo-pic'>
            <img src={LogoPic}/>
          </div> 
           
          <div className='footer-links'>
            <a href='#'> <br/> </a>
          </div>
          
          <div className='footer-button'>
            <button> </button>
          </div>           
        </div>

        <div className='footer-links-section'>
          <div className='footer-information information-services'>
           <h1></h1>
           <Link
               to="Intro" spy={true} smooth={true} offset={-50} duration={500} 
              ></Link>
              <Link
               to="Service" spy={true} smooth={true} offset={50} duration={500} 
              ></Link>
              <Link
               to="Ourwork" spy={true} smooth={true} offset={50} duration={500} 
              > </Link>
              <Link
               to="ValidationForm" spy={true} smooth={true} offset={-50} duration={500} 
              ></Link>
          </div>
          
          <div className='footer-services information-services'>
           <h1></h1>
           <a href='#'>  </a>
           <a href='#'>   </a>
           <a href='#'>   </a>
           <a href='#'></a>
          </div>
        </div> 
      </div>

      <div className='footer-bottom'>
        <span>&copy; 2021   </span>
      </div>
    </div>
    </>
  )
}

export default Footer