import React from 'react';


import "swiper/css";
import "swiper/css/navigation";
import { Navigation } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";

import AliBaba from '../assets/svg/alibaba-logo.svg';
import GoogleLogo from '../assets/svg/google-logo.svg';
import YandexLogo from '../assets/svg/yandex-logo.svg';
import Macys from '../assets/svg/macys-logo.svg';


const Ourwork = () => {
  return (
    <>
    <div className='our-work' name="Ourwork">
        <div className='our-work-header'>
          <h1> </h1>
        </div>
        
        <div className='our-work-cards'>
        <Swiper
        // slidesPerView={1}
        // spaceBetween={5}
        slidesPerGroup={2}
         loop={true}
         loopFillGroupWithBlank={true}
        // pagination={{
        //   clickable: true,
        // }}
        navigation={true}
        breakpoints={{
          640: {
            slidesPerView: 1,
            spaceBetween: 10,
          },
          768: {
            slidesPerView: 1,
            spaceBetween: 10,
          },
          1224: {
            slidesPerView: 2,
            spaceBetween: 0,
          },
        }}
        modules={[ Navigation]}
        className="mySwiper"
      >
      
      <SwiperSlide> 
      <div className='our-work-card'>
          <div className='our-work-logo'>
            <img src={AliBaba}/>
            <p>
                       
                     
                 
            </p>
          </div>    
               
          <div className='our-work-result'>
                
           <div className='result-top'>
              <h4>:</h4>
           </div> 
           
           <div className='result-bottom'>
                   <div className='result-percentages '>
                     <span>+85,71%</span>
                     <small>     </small>
                   </div>

                   <div className='result-percentages'>
                     <span>+83,51%</span>
                     <small> </small>
                   </div>

                   <div className='result-percentages'>
                     <span>-50,51%</span>
                     <small>   </small>
                   </div>
           </div>

          </div>    

          <div className='system-section'>
            <div className='system-header'>
              <h3>:</h3>
            </div>
            <div className='system-names'>
              <img src={GoogleLogo}/>
              <img src={YandexLogo}/>
            </div>
          </div>    
      </div>          
      </SwiperSlide>        
      
      <SwiperSlide>
        <div className='our-work-card'>
         <div className='our-work-logo'>
          <img src={Macys}/>
          <p>
                  
                
            
          </p>
         </div>   
             
         <div className='our-work-result'>
                
            <div className='result-top'>
              <h4>:</h4>
            </div> 
               
            <div className='result-bottom'>
              <div className='result-percentages '>
                <span>+85,71%</span>
                <small>     </small>
              </div>
              <div className='result-percentages'>
                <span>+83,51%</span>
                <small> </small>
              </div>
              <div className='result-percentages'>
                <span>-50,51%</span>
                <small>   </small>
              </div>
            </div>

         </div>   
         
         <div className='system-section'>
            <div className='system-header'>
              <h3>:</h3>
            </div>
            <div className='system-names'>
              <img src={GoogleLogo}/>
              <img src={YandexLogo}/>
            </div>
         </div>   
        </div>     
      </SwiperSlide>   
  
      </Swiper>
     
      </div>

        
    </div>
    </>
  )
}

export default Ourwork