import React from 'react';

import IntroPic from '../assets/images/intro-pic.png'

const Intro = () => {
  return (
    <>
    <div className='intro-section' name="Intro">
        <div className='intro-left-side'>
           <div className='header'>
            <p className='top'> </p>
            <p className='bottom'> </p> 
            <span>     </span>
           </div>

           <div className='header-buttons'>
            <button> </button>
            <button></button>
            <button></button>
           </div>

        </div>

       <div className='intro-right-side'>
        <img  src={IntroPic}/>
       </div> 

    </div>
    </>
  )
}

export default Intro