import React  from 'react';

import swal from 'sweetalert';
import FormPic from '../assets/images/form-pic.png'
import {useForm} from 'react-hook-form' ;

  const ValidationForm = () => {
  
  const {register, handleSubmit, formState:{errors}} = useForm();

   
  return (
    <>
    <div className='form-section' name="ValidationForm">
        <div className='form-section-left'>
          
        <div className='form-header'>
            <h1>   </h1>
            <span>         !</span>
        </div>
        
        <div className='form-page'>
          
          <form onSubmit={handleSubmit (()=>
            swal(" !", "  .", "success")
           )}>
           <div className='name-phone-inputs'>
             
             <div className='user-name-input'>
              <input  placeholder=' ' type='text'
                {...register("userName", {
                required: true,
                pattern: /^[A-Za-z]+$/i
                })}
              /> 
              {errors?.userName?.type === "required" && <span>    </span>}
              {errors?.userName?.type === "pattern" && (<span>  </span>)}
             </div>
             
             <div className='phone-number-input'>
              <input   placeholder='  ' type="text" 
                {...register("phoneNumber", {
                required: true,
                pattern: /^\+?([0-9]{3})\)?[]?([0-9]{3})[]?([0-9]{2})[]?([0-9]{2})$/
                })}
              />
              {errors?.phoneNumber?.type === "required" && <span>    </span>}
              {errors?.phoneNumber?.type === "pattern" && (<span>  , : +994-xxx-xx-xx</span>)}
             </div>
             
           </div>
           
           <div className='question-area'>
            <textarea type='text' placeholder=' '
            {...register("question", {
                required: true,
                })}
            >
            </textarea>
            {errors?.question?.type === "required" && <span>    </span>}
           </div>
           
           <div className='check-boxes'>
           
           <div className='checked'>
             <label className='check-box'>
               <input type="checkbox"
                {...register("checkbox",{
                  required: true
                })}
               />
               <span className='checkmark' ></span>
             </label>  
              <p>       </p>
            </div>  
            {errors?.checkbox?.type === "required" && <span>     </span>} 
           
           </div>
            
           <div className='submit-btn'>
            <button></button>
           </div>
           
          </form>  
        
        </div>
        
        </div>

        <div className='form-section-right'>
            <img src={FormPic}/>
        </div>
    </div>
    </>
  )
  }

export default ValidationForm