import React from 'react';
import { useState } from 'react'; 

import { Link, Button, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'

import LogoPic from '../assets/images/logo-pic.png';
import MenuIcon from '../assets/svg/menu-icon.svg';

const Navbar = () => {
   
    const [isNavExpanded, setIsNavExpanded] = useState(false);

    const openMenu = () => {
        setIsNavExpanded(!isNavExpanded)
    }
    
  
    return (
    <>
        <div className='nav-bar'>
            <div className='logo-section'>
              <img className='logo' src={LogoPic}></img>
            </div>
            
            <button className='menu-icon' onClick={openMenu} >               
               <img src={MenuIcon}/>
            </button>
            
            
            <div className={ isNavExpanded ? "nav-links-expanded" : "nav-links"}>
            
              <Link
               to="Intro" spy={true} smooth={true} offset={-50} duration={500} 
              ></Link>
              <Link
               to="Service" spy={true} smooth={true} offset={50} duration={500} 
              ></Link>
              <Link
               to="Ourwork" spy={true} smooth={true} offset={50} duration={500} 
              > </Link>
              <Link
               to="ValidationForm" spy={true} smooth={true} offset={-50} duration={500} 
              ></Link>
            
            </div>

            <div className='button-section'>
              <button className='btn'> </button>
            </div> 

        </div>
    </>
  )
}

export default Navbar